import React, { Component } from 'react'
import { Button } from 'react-bootstrap';

import LastVenueWidgetContent from './LastVenueWidgetContent';
import ChampResWidgetContent from './ChampResWidgetContent';
import CalendarWidgetContent from './CalendarWidgetContent';

class CalResWidget extends Component {
    constructor(){
        super()
        this.state = {
            pickedWidget: "CAL"
        }
        this.handleChange = this.handleChange.bind(this)
        this.renderElement = this.renderElement.bind(this)
    }

    handleChange(id) {
        console.log(id)
        this.setState(prevState => {
            return {
                pickedWidget: id
            }
        })
    }

    renderElement() {
        console.log(this.state)
        if(this.state.pickedWidget === "CAL") {
            return <CalendarWidgetContent />
        }
        if(this.state.pickedWidget === "LRES") {
            console.log("returning LRES")
            return <LastVenueWidgetContent />
        }
        if(this.state.pickedWidget === "CRES") {
            return <ChampResWidgetContent />
        }
        return null;
    }

    render() {
        return (
            <div className="calResWidget flex-d flex-sb">
                <span id="quickNewsTitle" className="reGrayTitle">Boxová tabuľa</span>
                <div className="widgetButtonContainer flex-r flex-sb ifont fs-12">
                    <div className={`widgetSButtonContainer ${this.state.pickedWidget === "CAL" ? "selected" : ""}`}>
                        <Button onClick={() => this.handleChange("CAL")}
                                className={`widgetButton ${this.state.pickedWidget === "CAL" ? "selected" : ""}`}>
                            Budúca VC
                        </Button>
                        <div className="arrow-up"></div>
                    </div>
                    <div className={`widgetSButtonContainer ${this.state.pickedWidget === "LRES" ? "selected" : ""}`}>
                        <Button onClick={() => this.handleChange("LRES")} 
                                className={`widgetButton ${this.state.pickedWidget === "LRES" ? "selected" : ""}`}>
                            Posledná VC
                        </Button>
                        <div className="arrow-up"></div>
                    </div>
                    <div className={`widgetSButtonContainer ${this.state.pickedWidget === "CRES" ? "selected" : ""}`}>
                        <Button onClick={() => this.handleChange("CRES")} 
                                className={`widgetButton ${this.state.pickedWidget === "CRES" ? "selected" : ""}`}>
                            Šampionát
                        </Button>
                        <div className="arrow-up"></div>
                    </div>
                </div>
                <div className="calResWidgetContent">
                    <this.renderElement />
                </div>
            </div>
        )
    }
}
export default CalResWidget